body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: left;
  font: Helvetica, sans-serif;
}

.Toolbar {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  height: 80px;
  background-color: #ffffff;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 17s linear;
          animation: App-logo-spin infinite 17s linear;
  height: 80px;
  pointer-events: none;
}

.Button {
  height: 40px;
}

.CTA {
  height: 60px;
  width: 160px;
}

.parentContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  width: 100%;
  -webkit-align-content: center;
          align-content: center;
}

.containerLabel {
  opacity: 0.8;
  background-color: #000000;
  color: #ffffff;
  width: 20%;
  height: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.containerLabel p {
  opacity: 1.0;
  color: white;
  padding-left: 10px;
  font-size: 20pt;
}

.containerMain {
  width: 100%;
  height: 500px;
  border: 5px solid red;
  margin-left: 100px;
  margin-right: 100px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-content: center;
          align-content: center;
  text-align: center;
  background-image: url("https://images.unsplash.com/photo-1540908187087-eeabb5040af5?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1350&q=80");
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.containerLeft {
  width: 40%;
  margin-left: 30px;
  display: -webkit-flex;
  display: flex;
  text-align: center;
  -webkit-align-content: center;
          align-content: center;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.img-left {
  height: 400px;
  width: 100%;
  background-image: url("https://images.unsplash.com/photo-1540908187087-eeabb5040af5?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1350&q=80");
  background-size: 150%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;
}

.img-main {
  height: 100%;
  width: 100%;
  background-image: url("https://images.unsplash.com/photo-1540908187087-eeabb5040af5?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1350&q=80");
  background-size: 150%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;
}

.img-right {
  height: 400px;
  width: 100%;
  background-image: url("https://images.unsplash.com/photo-1505170988624-4abfd7fa954b?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1355&q=80");
  background-size: 150%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;
}

.lefttext {
  text-align: center;
  margin-top: 180px;
  color: white;
}

.righttext {
  text-align: center;
  margin-top: 180px;
  color: white;
}

.Body {
  display: -webkit-flex;
  display: flex;
  margin-top: 40px;
  margin-bottom: 40px;
}

.body-content {
  text-align: center;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

.text-section {
  width: 40%;
  margin-left: 30px;
  text-align: left;
  background-color: #666666;
}

/* Voter Issue CSS here */

.issue-body {
  margin-top: 104px;
}

.issue-body-content {
  text-align: left;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.single-issue {
  margin-right: 40px;
  width: 35%;
  border: 3px solid black;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  padding-left: 20px;
  padding-right: 20px;
}

.issue-header {
  font-size: 18px;
  font-weight: bold;
}

.page-header {
  text-align: center;
  width: 100%;
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
